import React, { useEffect, useState } from 'react';
import { useQuery } from 'react-query'; // Import react-query
import './Fotos.css'; // Import your CSS styles

// Function to fetch photo URLs
const fetchPhotos = async () => {
  const totalPhotos = 8; // Total number of photos (adjust as necessary)
  const basePath = `${process.env.PUBLIC_URL}/img/fotos/foto`;
  const fileExtension = '.jpg';

  // Create an array of photo URLs
  const photos = Array.from({ length: totalPhotos }, (_, index) => {
    return `${basePath}${totalPhotos - index}${fileExtension}`;
  });

  return photos;
};

function Fotos() {
  const { data: photos = [], isLoading, isError } = useQuery('photos', fetchPhotos); // Fetch photos with react-query
  const [photosPerPage, setPhotosPerPage] = useState(6); // Set default photos per page
  const [currentPage, setCurrentPage] = useState(1); // Set default page index

  // Update photos per page based on screen size
  useEffect(() => {
    const updatePhotosPerPage = () => {
      const screenWidth = window.innerWidth;

      // Set the number of photos to show based on screen size
      if (screenWidth < 576) {
        setPhotosPerPage(1); // Mobile: 1 photo per page
      } else {
        setPhotosPerPage(6); // Tablet and Desktop: 6 photos per page
      }
    };

    updatePhotosPerPage(); // Initial call
    window.addEventListener('resize', updatePhotosPerPage); // Update on resize

    return () => {
      window.removeEventListener('resize', updatePhotosPerPage); // Cleanup listener
    };
  }, []);

  // Reset current page when photos change
  useEffect(() => {
    setCurrentPage(1);
    console.log("rttt") // Reset to the first page when the photos array changes
  }, [photos]);

  // Calculate the current photos to show
  const indexOfLastPhoto = currentPage * photosPerPage;
  const indexOfFirstPhoto = indexOfLastPhoto - photosPerPage;
  const currentPhotos = photos.slice(indexOfFirstPhoto, indexOfLastPhoto);
  const totalPages = Math.ceil(photos.length / photosPerPage);

  if (isLoading) return <div>Loading...</div>;
  if (isError) return <div>Error loading photos!</div>;

  return (
    <div className="container mt-4">
      <h1 className="text-center">Fotos van onze blije klantjes</h1>

      <div className="row">
        {currentPhotos.map((photo, index) => (
          <div className="col-12 col-sm-6 col-md-4 mb-4" key={index}>
            <div className="foto-gallery-item">
              <img src={photo} alt={`Photo ${index + 1}`} className="foto-img img-fluid" />
            </div>
          </div>
        ))}
      </div>

      {/* Bootstrap Pagination */}
      <nav aria-label="Page navigation">
        <ul className="pagination justify-content-center">
          <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
            <button className="page-link" onClick={() => setCurrentPage(currentPage - 1)}>&laquo; Previous</button>
          </li>
          {Array.from({ length: totalPages }, (_, index) => (
            <li className={`page-item ${currentPage === index + 1 ? 'active' : ''}`} key={index}>
              <button className="page-link" onClick={() => setCurrentPage(index + 1)}>
                {index + 1}
              </button>
            </li>
          ))}
          <li className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}>
            <button className="page-link" onClick={() => setCurrentPage(currentPage + 1)}>Next &raquo;</button>
          </li>
        </ul>
      </nav>
    </div>
  );
}

export default Fotos;
